import React, { Suspense } from "react";
import '../../style.css';
import { withTranslation } from "react-i18next";
import { Helmet } from "react-helmet";

const Logo = React.lazy(()=>import("../../Component/imageComponents/logo"));
const SFC = React.lazy(()=>import("../../Component/imageComponents/sfc"));
const ST = React.lazy(()=>import("../../Component/imageComponents/ST"));
const RP = React.lazy(()=>import("../../Component/imageComponents/RP"));
const BLT = React.lazy(()=>import("../../Component/imageComponents/blt"));
const ProductSection = React.lazy(()=>import("../Home/Sections/productSection"));
const AwardsNFeatured = React.lazy(()=>import("./Sections/awardsNfeatured"));
const TabSection = React.lazy(()=>import("./Sections/tabSection"));
const NumberStats = React.lazy(()=>import("./Sections/numberStatsSection"));
const Testimonials = React.lazy(()=>import("../../Component/Testimonials/Testimonials.jsx"));
const APISection = React.lazy(()=>import("./Sections/apiSection"));

export class Home extends React.Component {

  constructor() {
    super()

    this.state = {
      userInteracted: false

    }
  }

  formatNumber = (value) => {
    return (value).toLocaleString('en-IN', {
      maximumFractionDigits: 0
    })
  }

  handleUserInteraction = ()=>{
    if (!this.state.userInteracted) this.setState({userInteracted:true});
  }

  render() {

    const { t } = this.props;

    return (
      <section onClick={this.handleUserInteraction} onScroll={this.handleUserInteraction} onTouchStart={this.handleUserInteraction} onMouseMove={this.handleUserInteraction} >

        <Helmet>
          <meta charSet="utf-8" />
          <title>{t('MetaTitle:mT1')}</title>
          <meta name="description" content={t('MetaDescription:mD1')} />
        </Helmet>
        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-7 col-md-6 mb-4 main-box">
              <h1>{t("Home:hOME1")}</h1>
              <p>{t("Home:hOME2")}</p>
              <a href="https://app.nivesh.com/home/index">
                <button type="button" className="btn btn-primary btn-lg">
                  {t('CTAFooter:InvestNow1')}
                </button>
              </a>
            </div>
            <div className="col-lg-5 col-md-6">
            <Suspense fallback={<div>Loading....</div>}><Logo/></Suspense>
            </div>
          </div>
        </div>

        <div id="goals">
          <div className="container container-wrapper">
            <h2>{t("Home:hOME3")}</h2>
            <h6 className="text-center">{t("Home:hOME4")}</h6>
            <div className="row">
              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card h-100">
                <Suspense fallback={<div>Loading....</div>}><SFC/></Suspense>
                  <div className="card-body">
                    <h5 className="card-title">{t("Home:hOME5")}</h5>
                    <p className="small-text">{t("Home:hOME6")}</p>
                  </div>
                  <div className="card-footer text-center">
                    <a href="/save-for-children" className="btn btn-primary">
                      {t('CTAFooter:MoreButton')}
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card h-100">
                <Suspense fallback={<div>Loading....</div>}><ST/></Suspense>
                  <div className="card-body">
                    <h5 className="card-title">{t("Home:hOME7")}</h5>
                    <p className="small-text">{t("Home:hOME8")}</p>
                  </div>
                  <div className="card-footer text-center">
                    <a href="/save-tax" className="btn btn-primary">
                      {t('CTAFooter:MoreButton')}
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card h-100">
                <Suspense fallback={<div>Loading....</div>}><RP/></Suspense>
                  <div className="card-body">
                    <h5 className="card-title">{t("Home:hOME9")}</h5>
                    <p className="small-text">{t("Home:hOME10")} </p>
                  </div>
                  <div className="card-footer text-center">
                    <a href="/plan-for-retirement" className="btn btn-primary">
                      {t('CTAFooter:MoreButton')}
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card h-100">
                <Suspense fallback={<div>Loading....</div>}><BLT/></Suspense>
                  <div className="card-body">
                    <h5 className="card-title">{t("Home:hOME11")}</h5>
                    <p className="small-text">{t("Home:hOME12")}</p>
                  </div>
                  <div className="card-footer text-center">
                    <a href="/build-long-term-wealth" className="btn btn-primary">
                      {t('CTAFooter:MoreButton')}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div id="achieve-goal">
          <div className="container container-wrapper">
            <h2>{t("Home:hOME13")}</h2>
            <h6 className="text-center text-white">{t("Home:hOME14")}</h6>
            <div className="row row justify-content-md-center">
              <div className="col-lg-4 col-md-6 mb-4">
                <div className="card h-100">
                  <div className="card-body">
                    <h3>{t("Home:hOME15")}</h3>
                    <p>{t("Home:hOME16")}</p>
                    <ul className="small-text">
                      <li>{t("Home:hOME17")}</li>
                      <li>{t("Home:hOME18")}</li>
                      <li>{t("Home:hOME19")}</li>
                      <li>{t("Home:hOME20")}</li>
                      <li>{t("Home:hOME21")}</li>
                    </ul>
                  </div>

                </div>
              </div>
              <div className="col-lg-4 col-md-6 mb-4">
                <div className="card h-100">
                  <div className="card-body">
                    <h3>{t("Home:hOME22")}</h3>
                    <p>{t("Home:hOME23")}</p>
                    <ul className="small-text">
                      <li>{t("Home:hOME24")}</li>
                      <li>{t("Home:hOME25")}</li>
                      <li>{t("Home:hOME26")}</li>
                      <li>{t("Home:hOME27")}</li>
                      <li>{t("Home:hOME28")}</li>
                    </ul>
                  </div>

                </div>
              </div>
            </div>
            <div className="text-center ctaBtnOur">
              <a href="/partner">{t("Home:hOME29")}</a>
            </div>
          </div>
        </div>

        {this.state.userInteracted &&<>
          <Suspense fallback={<div>LoadingSecion....</div>}><ProductSection/></Suspense>
          <Suspense fallback={<div>LoadingSecion....</div>}><APISection/></Suspense>
          <Suspense fallback={<div>LoadingSecion....</div>}><TabSection/></Suspense>
          <Suspense fallback={<div>LoadingSecion....</div>}><NumberStats/></Suspense>
          <Suspense fallback={<div>LoadingSecion....</div>}><Testimonials/></Suspense>
          <Suspense fallback={<div>LoadingSecion....</div>}><AwardsNFeatured/></Suspense>
        </>}
      </section>
    );
  }
}

export default withTranslation()(Home);